<template>
    <div class="mineSet">
        <button type="button" class="mset_domain" @click="showClassify">当前使用后台: {{curDomain}}</button>
        <div class="mset_host">
            <button @click="showeditHost" type="button">重新设置域名</button>
            <ul v-show="reshowhost">
                <li><input type="text" placeholder="自己的后台" data-curtype="mineDomain" @input="inputfn" v-model="mineDomain"></li>
                <li><input type="text" placeholder="公司的后台" data-curtype="companyDomain" @input="inputfn" v-model="companyDomain"></li>
                <li><input type="text" placeholder="后接链接" data-curtype="addLink" @input="inputfn" v-model="addLink"></li>
                <li><button type="button" @click="saveHost">保存</button></li>
            </ul>
        </div>
        <popList :popDatas="popData" :popStatus="popStatus" @popChange="changClassify" @popConfirm="popConfirm"></popList>
    </div>
</template>
<script>
import Vue from 'vue'
export default {
    name:'mineset',
    data(){
        return{
            curDomain: this.hosts+'tp/index.php',
            popStatus: false,
            popData:{
                title:'选择后台',
                slots: [
                    {
                    flex: 1,
                    values: ['自己','公司'],
                    className: 'slot1',
                    textAlign: 'center',
                    valueKey:'2015-02'
                    }
                ],
            },
            mineDomain: this.mineDomain,
            companyDomain: this.companyDomain,
            addLink: this.addLink,
            reshowhost: false,
        }
    },
    mounted(){},
    methods:{
        showClassify:function(){
            console.log('有点击');
            this.popStatus?(this.popStatus = false):(this.popStatus = true);
        },
        changClassify:function(e){
            // 选择分类
            console.log(e);
        },
        popConfirm(e){
            // 确定分类
            console.log(e);
            if(e.curValue[0]=='自己'){
                this.ismine = true;
                Vue.prototype.hosts = 'http://www.linjianping.cn';
            }else{
                this.ismine = false;
                Vue.prototype.hosts = 'http://ljp.cn7.iswweb.cn';
            }
            this.curDomain = Vue.prototype.hosts+'tp/index.php';
            console.log(Vue.prototype.hosts);
            this.showMsg({text:'修改成功'});
        },
        showeditHost(){
            this.reshowhost = !this.reshowhost;
        },
        inputfn(e){
            var curel = e.target.dataset.curtype;
            this[curel] = e.target.value;
        },
        saveHost(){
            console.log(this.mineDomain);
            console.log(this.companyDomain);
            console.log(this.addLink);
            if(this.mineDomain){
                Vue.prototype.mineDomain = this.mineDomain;
            }
            if(this.companyDomain){
                Vue.prototype.companyDomain = this.companyDomain;
            }
            if(this.addLink){
                Vue.prototype.addLink = this.addLink;
            }
            this.showMsg({text:'修改成功'});
        }
    }
}
</script>
<style>
    .mineSet{
        padding: 30px 16px;
    }
    .mset_domain,
    .mset_host button{
        display: block;
        width: 100%;
    }
    .mineSet button{
        height: 45px;
        line-height: 45px;
        background-color: rgb(18, 150, 219);
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
    }
    .mset_host {
        margin-top: 40px;
    }
    .mset_host ul{
        margin-top: 20px;
    }
    .mset_host li{
        margin-bottom: 20px;
    }
    
    .mset_host li input{
        height: 40px;
        line-height: 40px;
        border: none;
        border-bottom: 1px solid rgb(230, 230, 230);
        width: 100%;
        margin: 0;
        padding: 0;
    }
</style>


